@import "globals";

$navigation-color: #ececec;

.AppRouter-sidebar {
  height: 100%;
  width: $sidebar-width;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: $navigation-color;
  overflow-x: hidden;
  padding-top: 20px;

  & > ul {
    padding-left: 0;
    list-style: none inside;
    & > li:not(.AppRouter-li-indent) {
      margin: 8px 0 8px 12px;
    }
  }
}

.AppRouter-sidebar-active {
  color: red !important;
}

.AppRouter-li-indent {
  margin: 8px 0 8px 25px;
  & > a {
    color: #1a1a1abf;
  }
}

.AppRouter-navbar {
  background-color: $navigation-color !important;

  a.active:not(:first-child) {
    color: red !important;
  }
}